import $runtime from 'nullstack/runtime';

import Nullstack from "nullstack"
import AccondioFaq from './AccondioFaq'

class Faq extends Nullstack {

  
    render() {
        return(
            <section class="faq">
            <div class="headline">
                <h2>
                    <span>PERGUNTAS MAIS</span>
                    <br />
                    <span>FREQUENTES</span>
                </h2>
            </div>

            <div class="cordeon" id="accordion">
            
            <AccondioFaq number="1" title="Qual  a durao do curso?" 
                content="O Curso Full Stack Turbo possui uma carga horria total de 120 horas, distribudas em aulas 100% online que voc pode acessar no seu prprio ritmo." />
            <AccondioFaq number="2" title="Como funciona o acesso ao curso?" 
                content="Aps a compra, voc ter acesso imediato e vitalcio ao curso atravs da plataforma Hotmart. Isso permite que voc revise o contedo sempre que desejar." />
            <AccondioFaq number="3" title="O curso oferece certificado?" 
                content="Sim, ao concluir o curso, voc receber um certificado de 120 horas, que poder ser adicionado ao seu currculo e perfil profissional." />

            <AccondioFaq number="4" title="Preciso ter conhecimentos prvios em programao?" 
                content="No  necessrio ter conhecimentos prvios. O Curso Full Stack Turbo  estruturado para atender desde iniciantes at profissionais que desejam aprofundar seus conhecimentos." /> 
            
            <AccondioFaq number="5" title="Terei suporte durante o curso?" 
                content="Sim, voc ter suporte total da nossa equipe, alm de acompanhamento direto do mentor Alisson Suassuna. Tambm poder participar da comunidade exclusiva no Discord para tirar dvidas, promover a troca de conhecimentos e a criao de um networking entre os alunos." />

            <AccondioFaq number="6" title="O que so os 30 projetos reais mencionados?" 
                content="So projetos prticos que voc desenvolver ao longo do curso, aplicando o que aprendeu em situaes do mundo real. Esses projetos ajudam a consolidar seu conhecimento e a preparar voc para o mercado de trabalho." />  
            
            <AccondioFaq number="7" title="Existe uma garantia de satisfao?" 
                content="Sim, oferecemos uma garantia de satisfao de 7 dias. Se voc no estiver satisfeito com o curso, poder solicitar o reembolso dentro desse perodo." /> 
    
            <AccondioFaq number="8" title="O curso  atualizado regularmente?" 
                content="Sim, o contedo do curso  revisado e atualizado regularmente para garantir que voc esteja aprendendo as tecnologias e prticas mais atuais do mercado, tudo SEM custos adicionais." />
            </div>

           
           
            
        </section>

        )
    };
}

export default Faq